.container {
    text-align: center;
    align-content: center;
}

.box {
    width: 80px;
    height: 80px;
    font-size: 28px;
    border: 1px solid black;
    display: inline-block;
    line-height: 80px;
}

h1 {
    color: goldenrod;
    font-size: 3em;
}

.board {
    margin: 20px 0;
    display: inline-flex;
    flex-wrap: wrap;
    width: 320px;
}
.winText{
    color: red;
}
label{
    font-size: 1.5em;
}
.btn {
    background-color: #4CAF50; /* Green */
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
}
.reset {
    background-color: #f44336;
}